<template>
    <div class="latest-info-wrap">

      <div class="div_bar">
        <div class="bar_title"></div>
      <div class="bar_title">></div>
      <div class="bar_title"></div>
      <div class="bar_title">></div>
      <div class="bar_title bar_title_current"></div>
      </div>

      <div class="li-top clearfix">
        <div class="li-top-l">
          
          <el-select v-model="seljdmc" placeholder="" class="ml5">
            <el-option
              v-for="item in optjdmc"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="li-top-r">
          
          <el-date-picker class="p_date ml5 mr15" size="mini" v-model="value1" type="date" placeholder="" ></el-date-picker>
          
          <el-date-picker class="p_date ml5" size="mini" v-model="value2" type="date" placeholder="" ></el-date-picker>
          <div class="btn_op btn_export" @click="search"></div>
        </div>
      </div>

      <div class="li-cont">
        <div class="li-cont-l">
          <table>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <tr v-for="(item,index) in dataList.list">
              <td class="linum">{{index + 1}}</td>
              <td>{{item.name}}</td>
              <td>{{item.num}}</td>
            </tr>
            <tr>
              <td class="linum"></td>
              <td></td>
              <td>{{dataList.allNum}}</td>
            </tr>
          </table>
        </div>
        <div class="li-cont-r" v-if="chartChange == 1">
          <streetInfo :data='streetData'></streetInfo>
        </div>
        <div class="li-cont-r" v-if="chartChange == 2">
          <msgNum :data='msgData'></msgNum>
        </div>
      </div>

    </div>

</template>

<script type="text/javascript">
  import streetInfo from 'components/echarts/streetInfo.vue'
  import msgNum from 'components/echarts/msgNum.vue'
  export default {
     name: 'latest-info',
    data() {
      return {
        chartChange:1,
        dataList:{},
        dataList1:{
          allNum:'120',
          list:[
            {
              name:'',
              num:'50'
            },
            {
              name:'',
              num:'30'
            },
            {
              name:'',
              num:'20'
            },
            {
              name:'',
              num:'15'
            },
            {
              name:'',
              num:'5'
            }
          ]
        },
        dataList2:{
          allNum:'75',
          list:[
            {
              name:'',
              num:'50'
            },
            {
              name:'',
              num:'20'
            },
            {
              name:'',
              num:'5'
            }
          ]
        },
        optjdmc: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
        seljdmc:'',
        value1:'',
        value2:'',
        streetData: {
          dataAxis:['','','','','','','','','','','','','','','',''],
          ydata:[220, 182, 191, 234, 290, 330, 310, 123, 442, 321, 90, 149, 210, 122, 133, 334],
          yMax:500
        },
        msgData: {
          title:'',
          legendData:['','','',''],
          data: [{value:335, name:''},
            {value:679, name:''},
            {value:1548, name:''},
            {value:50, name:''}
          ],
          colors:['#2ea9ff','#5ad2ff','#ffbe41','#ff762c']
        }
      }
    },
    created:function(){
      this.dataList = this.dataList1;
    },
    methods: {
      search() {
        if(this.seljdmc == ''){
          this.chartChange = 1;
          this.dataList = this.dataList1;
        }else if(this.seljdmc == ''){
          this.chartChange = 2;
          this.dataList = this.dataList2;
        }
        console.log(this.chartChange)
      }
    },
    components:{
      streetInfo,
      msgNum
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";
  /*SASS  assets/css/variable  mixin*/
  .ml5{margin-left:5px;}
  .mr15{margin-right:15px;}
  .latest-info-wrap{
    width: 1200px;
    margin: 0 auto;
    min-height:calc(100vh - 160px);
    .div_bar{
      padding-left: 10px;
      padding-top: 20px;
      padding-bottom: 20px;
    }
    .bar_title{
      font-size: 12px;
      color: #666666;
      display: inline-block;
    }
    .bar_title_current{
      color: #009aff;
    }
    .li-top{
      background:#fff;padding:20px;border-radius:4px;box-shadow:0 0 10px rgba(8,147,253,.1);
      .li-top-l{float:left;}
      .li-top-r{
        float:right;
      }
    }
    .li-cont{
     margin-top:20px;background:#fff;padding:30px;border-radius:4px;box-shadow:0 0 10px rgba(8,147,253,.1);overflow:hidden;
      .li-cont-l{
        float:left;padding-left:65px;
        table{
          width:290px;border-radius:6px;overflow:hidden;border:1px solid #ebebeb;
          th{padding:0 5px;}
          td{
            padding:0 5px;
            &.linum{
               background:#f5f5f5;
               border-right:1px solid #ebebeb !important;
             }
          }
        }
      }
      .li-cont-r{
        float:right;width:560px;height:350px;background:#ccc;margin-right:130px;
      }
    }
  }
  .btn_op {
    width: 80px;
    height: 26px;
    border-radius: 4px;
    text-align: center;
    line-height: 26px;
    font-size: 14px;
    cursor: pointer;
    display: inline-block;
  }
  .btn_export
  {
    border: solid 1px #ffa826;
    color: #ffa826;
    box-shadow: 0px 0px 2px #ffa826;
    margin-left:20px;
  }
  .btn_export:hover {
    background-color: #ffa826;
    color: #ffffff;
  }



</style>




