<template>
    <div class="check-list-wrap">
      <div class="check_main">
        <!---->
      <div class="div_bar">
        <div class="bar_title"></div>
        <div class="bar_title">></div>
        <div class="bar_title"></div>
        <div class="bar_title">></div>
        <div class="bar_title bar_title_current"></div>
      </div>

        <!---->
        <div class="div_search">
            <div class="search_title"></div>
            <div style="display: inline-block">
              <el-select style="width:110px;" v-model="streetId">
                <el-option
                  v-for="item in streetList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </div>
            <div class="search_title"></div>
            <div style="display: inline-block">
              <el-select style="width:110px;" v-model="typeId">
                <el-option
                  v-for="item in msgTypeList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </div>

            <div class="search_title"></div>
            <div style="display: inline-block">
              <el-select style="width:120px;" v-model="checkResult">
                <el-option
                  v-for="item in resultList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </div>

            <div class="search_title"></div>

            <div style="display: inline-block;">
              <el-date-picker class="p_date" size="mini" v-model="startDate" type="date" placeholder="" ></el-date-picker>
            </div>


            <div class="search_title"></div>
            <div style="display: inline-block;">
              <el-date-picker class="p_date" size="mini" v-model="endDate" type="date" placeholder="" ></el-date-picker>
            </div>
            <div class="btn_op btn_export" style="display: inline-block;margin-left: 20px;width: 70px;" @click="search"></div>

        </div>

        <!---->
        <div class="div_data">
          <div class="div_op">
            <!--<div class="btn_op btn_add" @click="addData"></div>-->
            <div class="btn_op btn_delete" @click="deleteData"></div>
            <div class="btn_op btn_export" @click="exportData"></div>
          </div>

          <div class="div_table">
            <table border="0" cellspacing="0" cellpadding="0" width="100%">
              <tr class="tr_th">
                <th class="th_ck">
                  <span class="label-block">
                    <el-checkbox v-model="isAllSelect" @change="ckall_change">&nbsp;</el-checkbox>
                   </span>
                </th>
                <th class="th_order"></th>
                <th class="th_address"></th>
                <th class="th_company"></th>
                <th class="th_phone"></th>
                <th class="th_contact"></th>
                <th class="th_remark"></th>
                <th class="th_netaddress"></th>
                <th class="th_net"></th>
                <th class="th_collecttime"></th>
                <th class="th_msgtype"></th>
                <th class="th_checkresult"></th>
                <th class="th_checktime"></th>
              </tr>
              <tbody class='tbody'>
              <tr v-show="dataList.length==0">
                <td colspan="10"></td>
              </tr>
              <tr v-show="dataList.length>0" class="tr_data" v-for="(item,index) in dataList">
                <td>
                   <span class="label-block">
                    <el-checkbox  v-model="item.isSelect" :label="item.id">&nbsp;</el-checkbox>
                   </span>
                </td>
                <td>{{index+1}}</td>
                <td>{{ item.address }}</td>
                <td>{{ item.company }}</td>
                <td>{{ item.phone }}</td>
                <td>{{ item.contact }}</td>
                <td>
                   <div v-if="item.remark==''" style="color:#3da714">{{ item.remark }}</div>
                   <div v-if="item.remark==''" style="color:#ea8f18">{{ item.remark }}</div>
                </td>
                <td>
                  <a class="div_href" target="_blank" href="https://www.baidu.com">{{ item.netaddress }}</a>
                </td>
                <td>{{ item.net }}</td>
                <td>{{ item.collecttime }}</td>
                <td>
                  <div style="color:#ffa626;font-size: 18px;font-weight:bolder;">{{ item.msgtype }}</div>
                </td>
                <td>
                  <div v-if="item.checkresult==''" style="cursor: pointer;" @click="editData">{{ item.checkresult }}</div>
                  <div v-if="item.checkresult==''" style="color: #f27649;cursor: pointer;" @click="editData">{{ item.checkresult }}</div>
                  <div v-if="item.checkresult==''" style="color: #ffa826;cursor: pointer;" @click="editData">{{ item.checkresult }}</div>
                </td>
                <td>{{ item.checktime }}</td>
              </tr>
              </tbody>

            </table>

          </div>

          <div class="div_pager"></div>
        </div>

        <!---->
        <shelterLayer v-show="isEditShow">
          <div class="div_win div_add" >
            <div>
               <div class="div_addrow">
                <div class="add_title">:</div>
                <div style="display: inline-block">
                  <el-date-picker class="p_date" size="mini" v-model="saveObj.checktime" type="date" placeholder="" ></el-date-picker>
                </div>
               </div>
              <div class="div_addrow">
                <div class="add_title">:</div>
                <div style="display: inline-block">
                  <el-select style="width: 135px;"  v-model="typeId">
                    <el-option
                      v-for="item in resultList"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </div>
              </div>
              <div class="div_addrow">
                <div  style="font-size: 16px;float: left;margin-right: 25px;">:</div>
                <div style="display: inline-block">
                  <textarea class="txtarea"></textarea>
                </div>
              </div>
            </div>

            <div style="margin-top: 20px;margin-left: 35%;">
              <div class="btn_op btn_add" style="display: inline-block;" @click="saveData"></div>
              <div class="btn_op btn_cancel" style="display: inline-block;" @click="cancelAdd"></div>
            </div>

          </div>

        </shelterLayer>

        <!---->
        <shelterLayer v-show="isExportShow">
          <div class="div_win div_export" >
            <div class="div_addrow" style="margin-bottom: 30px;">
              <div class="add_title">:</div>
              <div style="display: inline-block">
                <input class="input_txt" style="height: 28px;margin-right: 15px;" v-model="saveObj.address"/>
              </div>
              <input ref="fileId" type="file" @click="clickfile" v-show="false"/>
              <div class="btn_op btn_selectpath" style="display: inline-block;" @click="openPath"></div>
            </div>
            <div style="margin-left: 35%;">
              <div class="btn_op btn_add" style="display: inline-block;" @click="saveExport"></div>
              <div class="btn_op btn_cancel" style="display: inline-block;" @click="cancelExport"></div>
            </div>
          </div>
        </shelterLayer>

      </div>
    </div>

</template>

<script type="text/javascript">
  import shelterLayer from '../../../components/shelter/shelterLayer.vue'

  export default {
    name: 'check-list',
    data() {
      return {
        type: '',
        streetId: 0,
        typeId: 0,
        checkResult: '',
        isAllSelect: false,
        startDate: '',
        endDate: '',
        isEditShow: false,
        isExportShow: false,
        filePath: '',
        barSelectList: [true, false, false],
        streetList: [
          {id: 0, name: '', isSelect: false},
          {id: 1, name: '', isSelect: false},
          {id: 2, name: '', isSelect: false},
          {id: 3, name: '', isSelect: false},
          {id: 4, name: '', isSelect: false},
          {id: 5, name: '', isSelect: false},
          {id: 6, name: '', isSelect: false}
        ],
        msgTypeList: [
          {id: 0, name: ''},
          {id: 1, name: ''},
          {id: 2, name: ''},
          {id: 3, name: ''},
          {id: 4, name: ''}
        ],
        resultList: [
          {id: 0, name: ''},
          {id: 1, name: ''},
          {id: 2, name: ''},
        ],
        dataList: [
          {
            id: 1,
            address: '1445',
            company: '',
            phone: ' 021-66890 0510',
            contact: '-',
            remark: '',
            netaddress: '',
            net: '',
            collecttime: '2018-01-02',
            msgtype: '',
            checkresult: '',
            checktime: '2018-01-02',
            isSelect: false,
          },
          {
            id: 2,
            address: '99A',
            company: 'Logans ',
            phone: ' 021-66890 0510',
            contact: '-',
            remark: '',
            netaddress: '',
            net: '58',
            collecttime: '2018-01-02',
            msgtype: '',
            checkresult: '',
            checktime: '2018-01-02',
            isSelect: false,
          },
          {
            id: 3,
            address: '29',
            company: '',
            phone: ' 021-66890 0510',
            contact: '',
            remark: '',
            netaddress: '',
            net: '51job.com',
            collecttime: '2018-01-02',
            msgtype: '',
            checkresult: '',
            checktime: '2018-01-02',
            isSelect: false,
          },
        ],
        saveObj: {
          checktime: '',
          checkresult: '',
          detail: '',
        }
      }
    },
    methods: {
      search() {

      },

      ckall_change(val) {
        for (let item of this.dataList) {
          item.isSelect = val;
        }
      },

      deleteData() {

      },
      editData() {
        this.isEditShow = true;
      },
      exportData() {
        this.isExportShow = true;
      },
      saveExport() {
        this.isExportShow = false;
      },
      cancelExport() {
        this.isExportShow = false;
      },
      openPath() {
        this.$refs.fileId.click();
      },
      clickfile(val) {
        console.log('', val);
      },
      saveData() {
        this.isEditShow = false;
      },
      cancelAdd() {
        this.isEditShow = false;
      },
    },
    components: {shelterLayer},
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";
  /*SASS  assets/css/variable  mixin*/

  .check-list-wrap {
    width: 100%;
    min-height: calc(100vh - 160px);
    background-color: #eff2f9;
    overflow: hidden;
  }

   .check_main {

     width: 1200px;
     margin: 0 auto;
   }

  .div_bar
  {
    margin-top: 20px;
  }

  .bar_title
  {
    font-size: 12px;
    color: #666666;
    display: inline-block;
  }

  .bar_title_current
  {
    color: #63aefc;
    text-decoration: underline;
  }


  .div_search {
    margin-top: 30px;
    width: 1200px;
    background-color: #ffffff;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    border-radius: 5px;
    box-shadow: 0px 0px 9px 1px rgba(8, 147, 253, 0.1);
    padding-top: 20px;
    padding-bottom: 20px;
  }


  .search_title {
    font-size: 16px;
    display: inline-block;
    margin-left: 20px;
    margin-right: 5px;
  }


  .div_op
  {
    margin-left: 20px;
    margin-top: 30px;
    margin-bottom: 15px;
  }

  .btn_op {
    width: 80px;
    height: 26px;
    border-radius: 4px;
    text-align: center;
    line-height: 26px;
    font-size: 14px;
    cursor: pointer;
    display: inline-block;
    margin-right: 16px;
  }

  .btn_add
  {
    border: solid 1px #0893fd;
    color: #0893fd;
    box-shadow: 0px 0px 2px #0893fd;
  }

  .btn_add:hover {
    background-color: #0893fd;
    color: #ffffff;
  }

  .btn_delete
  {
    border: solid 1px #f27649;
    color: #f27649;
    box-shadow: 0px 0px 2px #f27649;
  }

  .btn_delete:hover {
    background-color: #f27649;
    color: #ffffff;
  }

  .btn_export
  {
    border: solid 1px #ffa826;
    color: #ffa826;
    box-shadow: 0px 0px 2px #ffa826;
  }
  .btn_export:hover {
    background-color: #ffa826;
    color: #ffffff;
  }

  .btn_cancel{
    border: solid 1px #c2c4cc;
    color: #c2c4cc;
    box-shadow: 0px 0px 2px #c2c4cc;
  }

  .btn_selectpath
  {
    width: 120px;
    height: 27px;
    border: solid 1px #ffa826;
    color: #ffa826;
    box-shadow: 0px 0px 2px #ffa826;
  }

  .btn_cancel:hover {
    background-color: #c2c4cc;
    color: #ffffff;
  }

  .btn_selectpath:hover {
    background-color: #f27649;
    color: #ffffff;
  }


  .div_data {
    width: 100%;
    height: 100%;
    border: 1px solid #ffffff;
    background-color: #ffffff;
    box-shadow: 0px 0px 9px 1px rgba(8, 147, 253, 0.1);
    box-sizing: border-box;
    border-radius: 5px;
    margin-top: 20px;
  }

  .div_table
  {
    width: 97%;
    border: 1px solid #e9e6e6;
    border-radius: 5px;
    overflow: hidden;
    box-sizing: border-box;
    margin-left: 20px;
    table-layout:fixed;
  }

  .th_ck{
    width: 2%;
  }
  .th_order{ width: 2%;}
  .th_address{ width: 10%;}
  .th_company{ width: 5%;}
  .th_phone{ width: 5%;}
  .th_contact{ width: 3%;}
  .th_remark{ width: 5%;}
  .th_netaddress{ width: 5%;}
  .th_net{ width: 5%;}
  .th_collecttime{ width: 5%;}
  .th_msgtype{ width: 5%;}
  .th_checkresult{ width: 5%;}
  .th_checktime{ width: 5%;}

  .div_href {
    color: #008aff;
    text-decoration: underline;
  }

  .div_pager {
    width: 100%;
    height: 80px;
  }

  .div_win
  {
    background: #ffffff;
    position: fixed;
    z-index: 10;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    border-radius: 5px;
    box-shadow: 0px 0px 3px #ffffff;
  }

  .div_add {
    width: 500px;
    height: 330px;
  }

  .div_export {
    width: 700px;
    height: 150px;
  }

  .div_addrow
  {
    margin-left: 30px;
    margin-top: 25px;
  }

  .add_title
  {
    font-size: 16px;
    margin-right: 15px;
    display: inline-block;
  }

  .input_txt
  {
    height: 30px;
    width: 350px;
    padding: 0 10px;
    /*color: #fff;*/
    outline: none;
    border-radius: 6px;
    border: 1px solid #c2c4cc;
    background: #ffffff;
    box-shadow: 0px 0px 2px #c2c4cc;
  }

  .txtarea
  {
    height: 100px;
    width: 300px;
    padding: 10px;
    border-radius: 6px;
    border: 1px solid #c2c4cc;
    background: #ffffff;
    box-shadow: 0px 0px 2px #c2c4cc;
  }

</style>





