<template>
  <div class="home-wrap">
    <div class="div_bar">
      <div class="bar_title"></div>
      <div class="bar_title">></div>
      <div class="bar_title"></div>
      <div class="bar_title">></div>
      <div class="bar_title bar_title_current"></div>
    </div>
    <div class="header">
      <div :class="this.isAreaOpen==true?'rowadreesopen':'rowadrees'">
        <div class="rowleft"></div>
        <div class="rowcenter">
          <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
        </div>
        <div class="checkboxgroup">
          <el-checkbox-group v-model="addressList" @change="handleCheckedCitiesChange">
            <el-checkbox  v-for="city in cities" :label="city" :key="city" >{{city}}</el-checkbox>
          </el-checkbox-group>
        </div>
        <div class="rowbutton">
          <i  :class="this.isAreaOpen==true?'el-icon-minus':'el-icon-plus'" style="color:#0092ff;cursor: pointer" @click="AreaOpen"></i>
        </div>
      </div>
      <div class="row">
        <div class="rowleft"></div>
        <div class="rowcenter">
          <el-checkbox :indeterminate="isIndeterminate1" v-model="checkAll1" @change="handleCheckAllChanges"></el-checkbox>
        </div>
        <div class="checkboxgroup">
          <el-checkbox-group v-model="typeList" @change="handleCheckedCitiesChanges">
            <el-checkbox  v-for="city in citiess" :label="city" :key="city" >{{city}}</el-checkbox>
          </el-checkbox-group>
        </div>
        <div class="rowbutton">
        </div>
      </div>
      <div class="row">
        <div class="rowleft"></div>
        <div class="checkboxgroup">
          <span style="padding-left: 20px;padding-right: 10px;"></span>
          <el-date-picker
            v-model="startdate"
            type="date"
            class="p_date"
            size="mini"
            placeholder="">
          </el-date-picker>
          <span style="padding-left: 100px;padding-right: 10px;"></span>
          <el-date-picker
            v-model="enddate"
            type="date"
            class="p_date"
            size="mini"
            placeholder="">
          </el-date-picker>
        </div>
        <div class="rowbutton">
            <el-button class="srechbutton" ></el-button>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="div_table">
      <table border="0" cellspacing="0" cellpadding="0">
          <tr class="tr_th">
            <th class="width5">
                 <span class="label-block">
                    <el-checkbox v-model="isAllSelect" @change="ckall_change">&nbsp;</el-checkbox>
                   </span>
            </th>
            <th class="width5"></th>
            <th class="width15"></th>
            <th class="width10"></th>
            <th class="width10"></th>
            <th class="width10"></th>
            <th class="width15"></th>
            <th class="width10"></th>
            <th class="width10"></th>
            <th class="width10"></th>
          </tr>
        <tbody class='tbody'>
          <tr  v-for="(val, index) in tableData" :key="index">
            <td>
                  <span class="label-block">
                    <el-checkbox  v-model="val.check" :label="val.id">&nbsp;</el-checkbox>
                   </span>
              </td>
            <td>{{index+1}}</td>
            <td>{{val.address}}</td>
            <td>{{val.dept}}</td>
            <td>{{val.Telephone}}</td>
            <td>{{val.Contacts}}</td>
            <td> <a href="#" target="_blank" style="color: #008aff;word-break:break-all;width: 190px;">{{val.Collectionadress}}</a></td>
            <td>{{val.Contactswebsite}}</td>
            <td>{{val.createdate}}</td>
            <td><span style="color: #ffa626; font-weight: 800; cursor: pointer">{{val.whowtype=="1"?'':''}}</span></td>
          </tr>
        </tbody>
        </table>

      </div>
      <div style="margin-right:20px;"><el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page.currentPage"
          :page-sizes="page.pageSizes"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="page.total">
        </el-pagination></div>

    </div>
  </div>
</template>

<script type="text/javascript">
  const addressArray =['', '', '', '', '6', '5', '4', '3', '2', '1'];
  const typeArray = ['', '', '', ''];
  export default {
    name: 'home',
    data(){
      return {
        isAllSelect: false,
        startdate:'',
        enddate:'',
        addressList:[],
        typeList:[],
        checkAll: false,
        checkAll1: false,
        isIndeterminate: false,
        isIndeterminate1: false,
        cities: addressArray,
        citiess: typeArray,
        isAreaOpen:false,
        checked:false,
        tableData: [
          {
            id: 1,
            check: false,
            address: '  1455',
            dept: '()',
            Telephone: '021-61766988 021-61766989',
            Contacts: '',
            Collectionadress: 'http://hotels.ctrip.com/1578931.html?isFull=F#ctm_ref=hod_sr_lst_dl_n_1_1',
            Contactswebsite: '',
            createdate: '2017-09-12',
            whowtype: '1',
          },
          {
            id: 2,
            check: false,
            address: '  1455',
            dept: '()',
            Telephone: '021-61766988 021-61766989',
            Contacts: '',
            Collectionadress: 'http://hotels.ctrip.com/1578931.html?isFull=F#ctm_ref=hod_sr_lst_dl_n_1_1',
            Contactswebsite: '',
            createdate: '2017-09-12',
            whowtype: '1',
          },
          {
            id: 3,
            check: false,
            address: '  1455',
            dept: '()',
            Telephone: '021-61766988 021-61766989',
            Contacts: '',
            Collectionadress: 'http://hotels.ctrip.com/1578931.html?isFull=F#ctm_ref=hod_sr_lst_dl_n_1_1',
            Contactswebsite: '',
            createdate: '2017-09-12',
            whowtype: '1',
          },
          {
            id: 4,
            check: false,
            address: '  1455',
            dept: '()',
            Telephone: '021-61766988 021-61766989',
            Contacts: '',
            Collectionadress: 'http://hotels.ctrip.com/1578931.html?isFull=F#ctm_ref=hod_sr_lst_dl_n_1_1',
            Contactswebsite: '',
            createdate: '2017-09-12',
            whowtype: '0',
          },
          {
            id: 5,
            check: false,
            address: '  1455',
            dept: '()',
            Telephone: '021-61766988 021-61766989',
            Contacts: '',
            Collectionadress: 'http://hotels.ctrip.com/1578931.html?isFull=F#ctm_ref=hod_sr_lst_dl_n_1_1',
            Contactswebsite: '',
            createdate: '2017-09-12',
            whowtype: '0',
          },
          {
            id:6,
            check: false,
            address: '  1455',
            dept: '()',
            Telephone: '021-61766988 021-61766989',
            Contacts: '',
            Collectionadress: 'http://hotels.ctrip.com/1578931.html?isFull=F#ctm_ref=hod_sr_lst_dl_n_1_1',
            Contactswebsite: '',
            createdate: '2017-09-12',
            whowtype: '0',
          },
        ],
        page:{
          currentPage: 1,  //
          pageSizes:[6, 6],  //
          pageSize:20,  //
          total:12   //
        }
      }
    },
    mounted() {

    },
    methods:{
      handleCheckAllChange(val) {
        this.addressList = val ? addressArray: [];
        this.isIndeterminate = val;
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      handleCheckAllChanges(val) {
        this.typeList = val ? typeArray: [];
        this.isIndeterminate1 = false;
      },
      handleCheckedCitiesChanges(value) {
        let checkedCounts = value.length;
        this.checkAll1 = checkedCounts === this.citiess.length;
        this.isIndeterminate1 = checkedCounts > 0 && checkedCounts < this.citiess.length;
      },
      AreaOpen() {
        this.isAreaOpen=!this.isAreaOpen;
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      },
      ckall_change(val) {
        for (let item of this.tableData) {
          item.check = val;
        }
      },
    },
    components:{
    }
  }
</script>

<style lang="scss" scoped>
  .srechbutton{
    padding-top: 6px;
    width: 80px;
    height: 30px;
    background-color: #ffffff;
    box-shadow: 0px 5px 9px 1px
    rgba(255, 158, 0, 0.15);
    border-radius: 5px;
    color: #ffa826;
    border: solid 1px #ffa826;
  }
  .srechbutton:hover{
    background-color: #ffa826;
    color: #ffffff;
  }
  .home-wrap {
    width: 1200px;
    margin: 0px auto;
    background-color: #eff2f9;
    min-height: calc(100vh - 160px);
    .div_bar
    {
      padding-left: 10px;
      padding-top: 20px;
      padding-bottom: 20px;
    }
    .bar_title
    {
      font-size: 12px;
      color: #666666;
      display: inline-block;
    }
    .bar_title_current
    {
      color: #009aff;
    }
    .header {
      min-height: 187px;
      max-height: 248px;
      background-color: #ffffff;
      box-shadow: 0px 0px 9px 1px
      rgba(8, 147, 253, 0.1);
      border-radius: 5px;
      .rowadrees
      {
        min-height: 62px;
        overflow: hidden;
        line-height: 62px;
        font-family: MicrosoftYaHei;
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        border-bottom: solid 1px #eeeeee;
        transition: max-height 1s;
      }
      .rowadreesopen
      {
        min-height: 124px;
        overflow: hidden;
        line-height: 62px;
        font-family: MicrosoftYaHei;
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        border-bottom: solid 1px #eeeeee;
        transition: max-height 1s;
      }
      .rowleft {
        width: 120px;
        float: left;
        text-align: center;
        height: 62px;
      }
      .rowcenter {
        width: 100px;
        float: left;
        height: 62px;
      }
      .rowbutton{
        float: right;
        margin-right: 20px;
        width:80px;
      }
      .row {
        height: 62px;
        line-height: 62px;
        font-family: MicrosoftYaHei;
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        border-bottom: solid 1px #eeeeee;
      }
      .checkboxgroup {
        float: left;
        height: 62px;
        width: 880px;
      }
      .el-checkbox{
        margin-left:  0px;
        margin-right:  30px;
      }
    }
    .content {
      width: 1200px;
      margin-top: 30px;
      padding-top: 21px;
      padding-bottom: 20px;
      background-color: #ffffff;
      box-shadow: 0px 0px 9px 1px rgba(8, 147, 253, 0.1);
      border-radius: 5px;
      text-align: right;
      .div_table
      {
        width: 96%;
        border: 1px solid #e9e6e6;
        border-radius: 5px;
        overflow: hidden;
        box-sizing: border-box;
        margin-left: 20px;
        table-layout:fixed;
      }
      .width5 {
        width: 5%;
      }
      .width10 {
        width: 10%;
      }
      .width15 {
        width: 15%;
      }
      .el-pagination
      {
        margin-top: 15px;
      }
    }

  }
</style>



