                    v-for="item in msgTypeList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </div>
            </div>

            <div class="div_addrow">
              <div class="add_title">:</div>
              <div style="display: inline-block">
                <el-select style="width:120px;" v-model="saveObj.importance">
                  <el-option
                    v-for="item in importList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </div>
            </div>

          </div>
          <div style="margin-top: 30px;margin-left: 35%;">
            <div class="btn_op btn_add" style="display: inline-block;" @click="saveData"></div>
            <div class="btn_op btn_cancel" style="display: inline-block;" @click="cancelAdd"></div>
          </div>
