            <div class="add_title">:</div>
            <div style="display: inline-block">
              <input class="input_txt" style="height: 28px;margin-right: 15px;" v-model="saveObj.address"/>
            </div>
            <input ref="fileId" type="file" @click="clickfile" v-show="false"/>
            <div class="btn_op btn_selectpath" style="display: inline-block;" @click="openPath"></div>
          </div>
          <div style="margin-left: 35%;">
            <div class="btn_op btn_add" style="display: inline-block;" @click="saveExport"></div>
            <div class="btn_op btn_cancel" style="display: inline-block;" @click="cancelExport"></div>
          </div>
        </div>
      </shelterLayer>

      </div>

    </div>

</template>

<script type="text/javascript">
  import shelterLayer from '../../../components/shelter/shelterLayer.vue'

  export default {

    name: 'important-info',
    data() {
      return {
        type: '',
        streetId: 0,
        typeId: 0,
        isAllSelect: false,
        startDate: '',
        endDate: '',
        isAddShow: false,
        isExportShow: false,
        filePath: '',
        barSelectList: [true, false, false],
        streetList: [
          {id: 0, name: '', isSelect: false},
          {id: 1, name: '', isSelect: false},
          {id: 2, name: '', isSelect: false},
          {id: 3, name: '', isSelect: false},
          {id: 4, name: '', isSelect: false},
          {id: 5, name: '', isSelect: false},
          {id: 6, name: '', isSelect: false}
        ],
        msgTypeList: [
          {id: 0, name: ''},
          {id: 1, name: ''},
          {id: 2, name: ''},
          {id: 3, name: ''},
          {id: 4, name: ''}
        ],
        importList: [
          {id: 0, name: ''},
          {id: 1, name: ''},
          {id: 2, name: ''},
        ],
        dataList: [
          {
            id: 1,
            streetName: '',
            address: '9017',
            relatedTime: '2018-02-01',
            content: '',
            msgType: '',
            importance: '',
            recordingTime: '2017-01-01',
            recordingUser: '',
            isSelect: false,
          },
          {
            id: 2,
            streetName: '',
            address: '9902',
            relatedTime: '2018-02-08',
            content: '',
            msgType: '',
            importance: '',
            recordingTime: '2017-01-01',
            recordingUser: '',
            isSelect: false,
          },
          {
            id: 2,
            streetName: '',
            address: '2916',
            relatedTime: '2018-02-15',
            content: '',
            msgType: '',
            importance: '',
            recordingTime: '2017-01-01',
            recordingUser: '',
            isSelect: false,
          },
        ],
        saveObj: {
          streetName: '',
          address: '',
          relatedTime: '',
          content: '',
          msgType: '',
          importance: '',
          recordingTime: '',
          recordingUser: '',
        }
      }
    },
    mounted() {

    },
    methods: {
      selectBar(index) {
        this.barSelectList = [false, false, false];
      },

      selectStreet(item, index) {
        this.streetList[index].isSelect = !this.streetList[index].isSelect;
      },

      ckall_change(val) {
        for (let item of this.dataList) {
          item.isSelect = val;
        }
      },

      addData() {
        this.isAddShow = true;
      },

      deleteData() {

      },
      exportData() {
        this.isExportShow = true;
      },
      saveData() {
        this.isAddShow = false;
      },
      cancelAdd() {
        this.isAddShow = false;
      },

      saveExport() {
        this.isExportShow = false;
      },
      cancelExport() {
        this.isExportShow = false;
      },
      search() {

      },
      openPath() {
         this.$refs.fileId.click();
      },
      clickfile(val) {
        console.log('', val);
      }
    },
    components: {shelterLayer}
  }
</script>

